@import 'global.scss';

:host {
  /**
     * @prop --grid-column-gap: The gap between the columns of the grid.
     * @prop --grid-row-gap: The gap between the the rows of the grid.
     */
  --grid-column-gap: #{$rwc-spacing--md};
  --grid-row-gap: #{$rwc-spacing--md};

  display: block;
}

.container {
  display: grid;
  column-gap: var(--grid-column-gap);
  row-gap: var(--grid-row-gap);

  @for $columns from 1 to 13 {
    &.columns-#{$columns} {
      grid-template-columns: repeat($columns, auto);

      &.auto {
        grid-template-columns: repeat($columns, 1fr);

        &.center {
          $totalColumns: $columns * 2;
          grid-template-columns: repeat($totalColumns, 1fr);

          &> ::slotted(*) {
            grid-column-end: span 2;
          }

          @for $orphan from 1 to $columns {
            &> ::slotted(*:nth-last-child(#{$orphan}):nth-child(#{$columns}n + 1)) {
              grid-column-start: #{$columns + 1 - $orphan};
            }
          }
        }

        &.right {
          @for $orphan from 1 to $columns {
            &> ::slotted(*:nth-last-child(#{$orphan}):nth-child(#{$columns}n + 1)) {
              grid-column-start: #{$columns - $orphan + 1};
            }
          }
        }

        &> ::slotted(*) {
          grid-column-end: span 1;
        }
      }
    }
  }
}
