/**
 * Converts the JSON string representation to JSON Object.\
 * Allows usage of single quotes.
 */
export function parseJSON(json: string): Object | Array<any> {
  let result = json.replace(/\'/g, '"');
  result = result.replace(/(\r\n|\n|\r)/gm, "");
  result = result.replace(/( {2})+/g, ' ');
  return JSON.parse(result);
}
