import { Component, Prop, h, Watch, FunctionalComponent, Element, Host, Listen, State } from '@stencil/core';
import { GridColumnCountsWithBreakpoint, GridAlignment, GridColumnCount, GridLayoutMode } from '../../../interfaces/components/layout/columns/grid';
import { parseJSON } from '../../../utils/data/json';
import { DocumentUtils } from '../../../utils/document/document-utils';

@Component({
  tag: 'rwc-grid',
  styleUrl: 'grid.scss',
  shadow: true
})
export class RwcGrid {
  /**
   * The reference to the host element.
   */
  @Element() host: HTMLRwcGridElement;

  /**
   * Number of columns.
   */
  @Prop({ reflect: true }) columns: GridColumnCount = 2;

  /**
   * Content layout mode.
   */
  @Prop({ reflect: true }) mode: GridLayoutMode = 'auto';

  /**
   * column positioning of last row.
   */
  @Prop({ reflect: true }) alignment: GridAlignment = 'center';

  /**
   * If enabled, breakpoints will be based on host size instead of window inner width.
   */
  @Prop() breakOnHostWidth: boolean = false;

  /**
   * The current column count.
   */
  @State() columnCount: number = 2;

  //#region Validation

  /**
   * Updates the current column count on columns property change.
   */
  @Watch('columns')
  validateColumnCount(): void {
    this.updateColumnCount();
  }

  //#endregion

  /**
   * Handles the resize event of the window.
   */
  @Listen('resize', { target: 'window' })
  onWindowResize(): void {
    this.updateColumnCount();
  }

  /**
   * Handles the StencilJS lifecycle event called before the first load of the component.
   */
  componentWillLoad(): void {
    this.updateColumnCount();
  }

  /**
   * Updates the current column count based on breakpoints configuration.
   */
  private updateColumnCount(): void {
    const columnCount = this.columns;
    if (this.columns.toString().match(/\d+/)[0] === columnCount.toString()) {
      this.columnCount = Number(columnCount);
    } else {
      let columnConfig = this.columns as string | GridColumnCountsWithBreakpoint;

      if (typeof columnConfig === 'string') {
        columnConfig = parseJSON(columnConfig) as GridColumnCountsWithBreakpoint;
      }

      let highestValidBreakpoint: string;
      let highestValidBreakpointInPixels = 0;

      let containerWidth = this.breakOnHostWidth ? this.host.offsetWidth : window.innerWidth;

      if (typeof containerWidth === 'undefined') {
        containerWidth = window.innerWidth;
      }

      for (const screenSize of Object.keys(columnConfig)) {
        const screenSizeInPixels = DocumentUtils.convertToPixels(screenSize);
        if (screenSizeInPixels >= highestValidBreakpointInPixels && containerWidth >= screenSizeInPixels) {
          highestValidBreakpoint = screenSize;
          highestValidBreakpointInPixels = screenSizeInPixels;
        }
      }

      this.columnCount = columnConfig[highestValidBreakpoint];
    }
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div
          class={ {
            container: true,
            [`columns-${ this.columnCount }`]: true,
            [this.alignment]: true,
            [this.mode]: true
          } }
        >
          <slot></slot>
        </div>
      </Host>
    );
  }
}
